.addgame-table {
  background: none;
  margin-bottom: 0;
}

.addgame-row input.addgame-input, .addgame-row select.addgame-select {
  color: var(--color-text);
  box-sizing: border-box;
  background: #23293a;
  border: 1px solid #2a3955;
  border-radius: 6px;
  outline: none;
  width: 100%;
  padding: 8px 10px;
  font-size: 1.1rem;
  transition: border .15s, box-shadow .15s;
}

.addgame-row input.addgame-input:focus, .addgame-row select.addgame-select:focus {
  border: 1.5px solid var(--color-header);
  z-index: 3100;
  position: relative;
  box-shadow: 0 0 0 2px #5fa8e955;
}

.addgame-remove-btn {
  color: #e66;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 1.5rem;
  line-height: 1;
  transition: background .15s;
}

.addgame-remove-btn:hover {
  background: #e66a;
}

.addgame-add-btn {
  background: var(--color-btn);
  color: #fff;
