  border: none;
  border-radius: 6px;
  margin-top: 10px;
  padding: 8px 18px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: background .2s;
  display: block;
  box-shadow: 0 2px 8px #0002;
}

.addgame-add-btn:hover {
  background: var(--color-btn-hover);
}

.addgame-date-input {
  color: var(--color-text);
  box-sizing: border-box;
  background: #23293a;
  border: 1px solid #2a3955;
  border-radius: 6px;
  outline: none;
  width: 100%;
  margin-top: 4px;
  padding: 8px 10px;
  font-size: 1.1rem;
  transition: border .15s, box-shadow .15s;
}

.addgame-date-input:focus {
  border: 1.5px solid var(--color-header);
  box-shadow: 0 0 0 2px #5fa8e955;
}

.addgame-modal-overlay {
  z-index: 2000;
  background: #0000008c;
  justify-content: center;
  align-items: center;
  animation: .2s fadeInModalBg;
  display: flex;
  position: fixed;
  inset: 0;
}

@keyframes fadeInModalBg {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.addgame-modal-content {
  color: #f5f5f5;
  background: linear-gradient(120deg, #23293a 80%, #2a3955 100%);
  border-radius: 18px;
  flex-direction: column;
  align-items: stretch;
  width: auto;
  min-width: 340px;
  max-width: 98vw;
  max-height: 92vh;
  padding: 2.2rem 1.7rem 1.7rem;
  animation: .22s cubic-bezier(.4, 1.6, .6, 1) fadeInModal;
  display: flex;
  position: relative;
  overflow-y: auto;
  box-shadow: 0 8px 40px #0008, 0 1.5px #5fa8e9;
}

@media (width <= 600px) {
  .addgame-modal-content {
    width: 98vw;
    min-width: 0;
    padding: 1.1rem .5rem 1.2rem;
  }
}

@keyframes fadeInModal {
  from {
    opacity: 0;
    transform: translateY(30px)scale(.98);
  }

  to {
    opacity: 1;
    transform: translateY(0)scale(1);
  }
}

.addgame-modal-content h2 {
  text-align: center;
  letter-spacing: .5px;
  color: #5fa8e9;
  margin-top: 0;
  margin-bottom: 1.1rem;
  font-size: 1.45rem;
  font-weight: 700;
}

.addgame-modal-content form {
  flex-direction: column;
  gap: 1.1rem;
  display: flex;
}

.addgame-table-wrapper {
  margin-bottom: .5rem;
}

.addgame-error {
  color: #b00;
  text-align: center;
  background: #ffeaea;
  border-radius: 5px;
  margin-bottom: .5rem;
  padding: .5rem .75rem;
  font-size: .98rem;
}

.addgame-modal-actions {
  justify-content: flex-end;
  gap: .5rem;
  margin-top: .5rem;
  display: flex;
}

.modal-close-btn {
  background: var(--color-btn);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: .5rem 1.1rem;
  font-size: 1rem;
  transition: background .15s;
}

.modal-close-btn:hover {
  background: var(--color-btn-hover);
}

.player-details-modal {
