}

.scryfall-loading-spinner, .scryfall-loading-spinner-inside {
  pointer-events: none;
  z-index: 3200;
  background: none;
  border: 2.5px solid #5fa8e9;
  border-top-color: #23293a;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  margin-top: -9px;
  animation: .7s linear infinite scryfall-spin;
  position: absolute;
  top: 50%;
  right: 8px;
}

@keyframes scryfall-spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.addgame-input::-webkit-calendar-picker-indicator {
  -webkit-appearance: none !important;
  display: none !important;
}

.addgame-input::-webkit-clear-button {
  -webkit-appearance: none !important;
  display: none !important;
}

.addgame-input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  display: none !important;
}

.addgame-input::-webkit-outer-spin-button {
  -webkit-appearance: none !important;
  display: none !important;
}

.addgame-input::-webkit-search-cancel-button {
  -webkit-appearance: none !important;
  display: none !important;
}

.addgame-input::-webkit-search-decoration {
  -webkit-appearance: none !important;
  display: none !important;
}

.addgame-input[type="search"]::-webkit-search-cancel-button {
  display: none;
}

.addgame-input::-ms-clear {
