}

.addgame-input::-ms-reveal {
  display: none;
}

.addgame-input {
  appearance: none;
}

.scryfall-popup {
  z-index: 4000;
  color: #eaf6ff;
  background: #23293a;
  border: 1.5px solid #5fa8e9;
  border-radius: 10px;
  outline: none;
  min-width: 220px;
  max-width: 340px;
  max-height: 240px;
  padding: .15rem 0;
  font-family: inherit;
  font-size: 1.08rem;
  animation: .18s cubic-bezier(.4, 1.6, .6, 1) fadeInScryfall;
  position: fixed;
  overflow-y: auto;
  box-shadow: 0 8px 32px #000b;
}

@keyframes fadeInScryfall {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.scryfall-popup-item {
  cursor: pointer;
  text-align: left;
  width: 100%;
  color: inherit;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  padding: 9px 18px;
  font-family: inherit;
  font-size: 1.08rem;
  transition: background .13s, color .13s;
}

.scryfall-popup-item:hover, .scryfall-popup-item.active, .scryfall-popup-item:focus {
  color: #5fa8e9;
  background: #263a53;
  outline: none;
}

.commander-thumb {
  object-fit: cover;
  vertical-align: middle;
  background: #23293a;
  border: 1.5px solid #5fa8e9;
  border-radius: 6px;
  flex-shrink: 0;
  width: 36px;
  height: 36px;
  margin-right: 8px;
  display: inline-block;
  box-shadow: 0 2px 8px #0003;
}
